var statusBarHeight = uni.getSystemInfoSync().statusBarHeight;
import dnIcon from '@/components/dn-icon/dn-icon.vue'
export default {
	components: {
		dnIcon
	},
	props:{
		isBack:{  //是否展示返回按钮
			type:Boolean,
			default:true,
		},
		navColor:{ //字体颜色包括图标
			type:String,
			default:'#fff',
		},
		navIconType:{ //左侧返回按钮的图标默认是左箭头
			type:String,
			default:'wangzuo',
		},
		navBgColor:{
			type:String,
			default:'#000000'
		}
		
	},
	data() {
		return {
			statusBarHeight: 0,
			allheight: 0,

		}
	},
	created() {
		this.statusBarHeight = statusBarHeight + 'px';
		this.allheight = (statusBarHeight + 44) + "px";
	}
}
